import React, { useState, useEffect } from 'react';


// import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import DetailInfo from './DetailInfo';
import GuideInfo from './GuideInfo';
import MapInfo from './MapInfo';
import PhotoInfo from './PhotoInfo';
import PartyInfo from './PartyInfo';


const Header = styled.header`
background-color: #488AEE;
// padding: 5px;
height: 130px;
text-align: center;
justify-content: center;
`;

const Logoimg = styled.img`
// padding: 10px;
height: 110px;
text-align: center;
display: inline-block;
cursor: pointer; /*       */
`;

const StyledButton = styled.button`
text-decoration: none;
color: inherit;
background-color: lightGray;
padding: 5px 10px;
margin-right: 15px;
margin-top: 10px;
border: none; /*   */
border-radius: 13px;
display: inline-block;
cursor: pointer;
transition: background-color 0.3s ease-in-out;

font-size: 13px; /*    */
font-weight: 400; /*    */
`;

const Linkdiv = styled.div`
background-color: #488AEE;
text-decoration: none;
color: inherit;
text-align: right;
transition: background-color 0.3s ease-in-out;
`;

const MyLink = styled(Link)`
text-decoration: none;
color: inherit;
padding: 5px 10px;
margin-left: 5px;
display: inline-block;
transition: background-color 0.3s ease-in-out;

font-size: 13px; /*    */
font-weight: 400; /*    */

`;

const Headerlist = styled.div`
background-color: #488AEE;
text-align: right;
padding-bottom: 5px;
padding-right: 10px;
`;


const Nevigatordiv = styled.div`
margin-top: 20px;
margin-bottom: 20px;
display: flex;
justify-content: center;
`
{/*    text-align*/}
const Listdiv1 = styled.div` 
    width: 100%;
    height: 100%;
    padding-bottom: 5px;
    text-align: center; 
    border-bottom: 2px solid Black;
`
const Listdiv2 = styled.div` 
    width: 100%;
    height: 100%;
    padding-bottom: 5px;
    text-align: center;
    border-bottom: 2px solid Black;
`
const Listdiv3 = styled.div` 
    width: 100%;
    height: 100%;
    padding-bottom: 5px;
    text-align: center;
    border-bottom: 2px solid Black;
`
const Listdiv4 = styled.div` 
    width: 100%;
    height: 100%;
    padding-bottom: 5px;
    text-align: center;
    border-bottom: 2px solid Black;
`
const Listdiv5 = styled.div` 
    width: 100%;
    height: 100%;
    padding-bottom: 5px;
    text-align: center;
    border-bottom: 2px solid Black;

`


const FestivalDetail = () => {
    const [activeTab, setActiveTab] = useState('detail'); //   'detail'
    const festData = {
        image: 'star.jpg',
        description: '2023       !',
        phone: '051-607-6362',
        address: '  ',
        start: '2023-10-25',
        end: '2023-10-30',
        time: '',
        place: ' ',
        fee: '',
        //detailIamge: 'star.jpg',
        //  ??
        //  
    };

    return (
        <div>
            <Linkdiv>
            <StyledButton></StyledButton>
            </Linkdiv>
        <Header>
        <Link to="/."> {/*   */}
            <Logoimg src="info.png" alt="Logo" />
        </Link>
            <Headerlist>
            
            <MyLink to ="/festival-list"> </MyLink>
            <MyLink to ="/partyall-list">  </MyLink>
            <MyLink to ="/my-party-list"> </MyLink>
            </Headerlist>
        </Header>

        <Nevigatordiv>
                <Listdiv1 style={{borderBlockEndColor: activeTab === 'detail' ? '#488AEE' : 'black', color: activeTab === 'detail' ? '#488AEE' : 'black'}}><button style={{Color: activeTab === 'detail' ? '#488AEE' : 'black', color: activeTab === 'detail' ? '#488AEE' : 'black'}} onClick={() => setActiveTab('detail')}> </button></Listdiv1>
                <Listdiv2 style={{borderBlockEndColor: activeTab === 'guide' ? '#488AEE' : 'black', color: activeTab === 'guide' ? '#488AEE' : 'black'}}><button style={{Color: activeTab === 'guide' ? '#488AEE' : 'black', color: activeTab === 'guide' ? '#488AEE' : 'black'}} onClick={() => setActiveTab('guide')}>  </button></Listdiv2>
                <Listdiv3 style={{borderBlockEndColor: activeTab === 'map' ? '#488AEE' : 'black', color: activeTab === 'map' ? '#488AEE' : 'black'}}><button style={{Color: activeTab === 'map' ? '#488AEE' : 'black', color: activeTab === 'map' ? '#488AEE' : 'black'}} onClick={() => setActiveTab('map')}></button></Listdiv3>
                <Listdiv4 style={{borderBlockEndColor: activeTab === 'photo' ? '#488AEE' : 'black', color: activeTab === 'photo' ? '#488AEE' : 'black'}}><button style={{Color: activeTab === 'photo' ? '#488AEE' : 'black', color: activeTab === 'photo' ? '#488AEE' : 'black'}} onClick={() => setActiveTab('photo')}> </button></Listdiv4>
                <Listdiv5 style={{borderBlockEndColor: activeTab === 'party' ? '#488AEE' : 'black', color: activeTab === 'party' ? '#488AEE' : 'black'}}><button style={{Color: activeTab === 'party' ? '#488AEE' : 'black', color: activeTab === 'party' ? '#488AEE' : 'black'}} onClick={() => setActiveTab('party')}> </button></Listdiv5>
        </Nevigatordiv>
        <div>
            {activeTab === 'detail' && <DetailInfo festData={festData}/>}
            {activeTab === 'guide' && <GuideInfo festData={festData}/>}
            {activeTab === 'map' && <MapInfo />}
            {activeTab === 'photo' && <PhotoInfo festData={festData}/>}
            {activeTab === 'party' && <PartyInfo />}

        </div>

        </div>
        
    );
};





    export default FestivalDetail;