import React, { useState } from 'react';
import { Navigate } from 'react-router';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router-dom';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #488aee; /*   */
  padding: 20px; /*    */
  min-height: 100vh;
  
`;

const LogoImage = styled.img`
  width: 400px; /*    */
  height: auto;
  margin-top: 0px;
  margin-bottom: 0px;
`;

const InputContainer = styled.div`

  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  
`;

const Input = styled.input`

  width: 300px;
  height: 40px;
  margin-bottom: 30px;
  margin-top: 30px;
  padding: 10px;
  border-radius: 200px;
  border: none;
  box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.4);
  
`;


const RoundedBox = styled.div`
  border-radius: 200px;
  width:700px;
  height:800px;
  padding: 70px;
  background-color: #91B9F5; /*   */
  box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.2); /*   */
  margin-top: 50px;
  
`;
const SignUpButton = styled.button`
display: block;
width: 200px; /*       */
background-color: #0066ff;
color: white;
border: none;
border-radius: 200px;
padding: 10px 20px;
font-size: 16px;
cursor: pointer;
box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.2);
margin-top:20px;
`;

const BackButton = styled.button`
display: block;
width: 200px; /*       */
background-color: #0066ff;
color: white;
border: none;
border-radius: 200px;
padding: 10px 20px;
font-size: 16px;
cursor: pointer;
box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.2);
margin-top:20px;
`;



const logobuife = '/info.png';


const SignUp3 =()=> {
  const location = useLocation();
  const userID = location.state?.userID || '';
  const [code, setCode] = useState('');

  
  const handleSubmit = async () => {
    try {

      
      const params = new URLSearchParams();
      params.append('code', code);


      const requestOptions = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
 
        },
        credentials : 'include',
        body: params.toString(),
      };
   
  

      const response = await fetch('https://port-0-server-cloudtype-4fju66f2clmyxbee6.sel5.cloudtype.app/members/new/emailCheck', requestOptions);
      
      if (!response.ok) {
        throw new Error('  .');
      }

      const data = await response.text();
      window.location.href = '/loginform'; // URL  //    
       console.log(data); //    

       //    
    } catch (error) {
      console.error(' :', error);
 
      //      
    }
  };
 
 return (
   <div className="App">
     <div className="mainpage">
       
       <div className="mainbox">
        <Container>
          <RoundedBox>
          <Link to="/.">
            <LogoImage src={logobuife} alt="Buife " />
          </Link>

        
            <InputContainer>

            <p> {userID} .  8  .</p>
            <Input
             type="text" 
             placeholder=" "
             onChange={(e) => setCode(e.target.value)}
            />

                    <SignUpButton 
                    type="button"
                    className="signupButton"
                    onClick={handleSubmit}
                    >
                    </SignUpButton>
                    
                    <Link to="/signup2">
                    <BackButton type="button"
                    className="BackButton">
                    </BackButton>
                    </Link>

            </InputContainer>
        
          </RoundedBox>
         </Container>
       </div>
     </div>
   </div>
 );
}



export default SignUp3;