    import React, { Component } from 'react';
    import { Link } from 'react-router-dom';
    import styled from 'styled-components';
    import { useNavigate } from 'react-router-dom';
    
    //    styled-components

    const StyledDiv = styled.div`
    padding-right: 15px; /* padding  */
    `;
    const StyledButton = styled.button`
    text-decoration: none;
    color: inherit;
    background-color: lightGray;
    padding: 5px 10px;
    border: none; /*   */
    border-radius: 13px;
    display: inline-block;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;

    font-size: 13px; /*    */
    font-weight: 400; /*    */
    `;

    const MyLink = styled(Link)`
    text-decoration: none;
    color: inherit;
    background-color: lightGray;
    padding: 5px 10px;
    margin-left: 10px;
    border-radius: 13px;
    display: inline-block;
    transition: background-color 0.3s ease-in-out;

    font-size: 13px; /*    */
    font-weight: 400; /*    */

    `;

    class Login extends Component {
    constructor(props) {
    super(props);
    this.state = {
        isLoggedIn: false, //     
    };
    }

    handleLogin = () => {
    //       
    this.setState({
        isLoggedIn: true, //   
        
    });
    };

    handleLogout = () => {
    //       
    this.setState({
        isLoggedIn: false, //   
    });
    };

    render() {
    const { isLoggedIn } = this.state;
    function Login() {
        const navigate = useNavigate();
        
        }
    return (
        <StyledDiv>
        {isLoggedIn ? ( // isLoggedIn     
            <div>
            <StyledButton onClick={this.handleLogout}></StyledButton>
            <MyLink to="/Mypage"></MyLink>
            </div>
        ) : (
            <MyLink to="loginform">
            <StyledButton onClick={this.handleLogin}></StyledButton>
            </MyLink>
        )}
        </StyledDiv>
    );
    }
    }

    export default Login;
