import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';


const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #488aee; /*   */
  padding: 20px; /*    */
  min-height: 95vh;
`;

const LogoImage = styled.img`
  width: 300px; /*    */
  height: auto;
  margin-top: 0px;
  margin-bottom: 0px;
`;

const InputContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
`;

const Input = styled.input`
  width: 300px;
  height: 40px;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 200px;
`;

const ImagesContainer = styled.div`
  display: flex;
  justify-content: space-between;
`;

const HiImage = styled.img`
  width: 250px; /*     */
  height: auto;
`;

const ExcitedImage = styled.img`
  width: auto; /*     */
  height: 150px;
`;
const RoundedBox = styled.div`
  border-radius: 200px;
  width:700px;
  padding: 70px;
  background-color: #91B9F5; /*   */
  box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.2); /*   */
  margin-top: 50px;
`;
const LoginButton = styled.button`
display: block;
width: 200px; /*       */
background-color: #0066ff;
color: white;
border: none;
border-radius: 200px;
padding: 10px 20px;
font-size: 16px;
cursor: pointer;
`;

const SignupContainer = styled.div`
  text-align: center;
  margin-top: 20px;
`;

const SignupLine = styled.div`
  width: 100%;
  border-top: 1px solid white;
  margin: 20px 0;
`;

const SignupText = styled.p`
  color: black;
  font-size: 20px;
  margin: 0;
`;

const SignupButton = styled.button`
  background-color: #0066ff;
  color: white;
  border: none;
  border-radius: 200px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
`;


const logobuife = '/info.png';
const hiImage = '/(hi).png';
const excitedImage = '/().png';


const LoginForm=()=> {
  const [userID, setuserID] = useState('');
  const [userPW, setuserPW] = useState('');
  const [loggedIn, setLoggedIn] = useState(false);

  const handleLogin = async () => {
    try {

      
      const params = new URLSearchParams();
      params.append('userID', userID);
      params.append('userPW', userPW);

      const requestOptions = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
 
        },
        credentials : 'include',
        body: params,
      };
      
  
      const response = await fetch('https://port-0-server-cloudtype-4fju66f2clmyxbee6.sel5.cloudtype.app/members/login', requestOptions);
      setLoggedIn(true);
      if (!response.ok) {
        throw new Error('  .');
      }
      
      const data = await response.text();
      //userID   alert
      window.location.href = '/'; // URL  //    
       console.log(data); //    
      

    
       //    
    } catch (error) {
      console.error(' :', error);
      alert('   !');
      //      
    }
  };

  
 return (
   <div className="App">
     <div className="mainpage">
       
       <div className="mainbox">
        <Container>
          <RoundedBox>
          <Link to="/.">
            <LogoImage src={logobuife} alt="Buife " />
          </Link>
        
            <InputContainer>
              <Input type="text" placeholder="" onChange={(e) => setuserID(e.target.value)}/>
              <Input type="password" placeholder="" onChange={(e) => setuserPW(e.target.value)}/>

  
            <LoginButton type="button" onClick={handleLogin}
            className="loginButton">
            </LoginButton>
            </InputContainer>
            
            <SignupLine />
          
            <SignupContainer>
              <SignupText> BUIFE  ?</SignupText>
                <Link to="/signup2"><SignupButton></SignupButton></Link>
                
            </SignupContainer>


            <ImagesContainer>
            <ExcitedImage src={excitedImage} alt="" />
            <HiImage src={hiImage} alt="" />
            
            </ImagesContainer>

              
          </RoundedBox>
         </Container>
       </div>
     </div>
   </div>
 );
}



export default LoginForm;