import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

    const PasswordChangeContainer = styled.div`
    text-align: center;
    padding-top: 10px;
    padding-bottom:20px;
    `;

    const PasswordInputContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    `;

    const PasswordInput = styled.input`
    margin-bottom: 10px;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 15%;
    `;

    const ShowPasswordIcon = styled.span`
    position: relative;
    top: -36.5px;
    left: 120px;
    cursor: pointer;
    font-size: 18px;
    color: #777;
    transition: color 0.3s;

    &:hover {
    color: #488AEE;
    }
    `;

    const RequirementText = styled.p`
    font-size: 12px;
    color: #777;
    `;

    const SubmitButton = styled.button`
    background-color: #488AEE;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    `;

    const PasswordChange = () => {
    const [password, setPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [confirmNewPassword, setConfirmNewPassword] = useState('');
    const [showPassword, setShowPassword] = useState(false);

    const handleSubmit = () => {
    //       
    alert('.');
    };
    // const [userDataArray, setUserDataArray] = useState([]);

    //     useEffect(() => {
    //     const fetchData = () => {
    //         fetch('https://cors-anywhere.herokuapp.com/https://port-0-buife-5mk12alp6foaqx.sel5.cloudtype.app/members')
    //         .then(response => {
    //             if (!response.ok) {
    //             throw new Error('Network response was not ok');
    //             }
    //             return response.json();
    //         })
    //         .then(data => {
    //             console.log(` :`, data); //   
    //             setUserDataArray(data); //    
    //         })
    //         .catch(error => {
    //             console.error('Error fetching user data:', error);
    //         });
    //     };

    //     fetchData();
    //     }, []);

    return (
    <PasswordChangeContainer>
        <h2> </h2>
        <PasswordInputContainer>
        <PasswordInput
            type={showPassword ? 'text' : 'password'}
            placeholder=" "
            value={password}
            onChange={(e) => setPassword(e.target.value)}
        />
        <ShowPasswordIcon onClick={() => setShowPassword(!showPassword)}>
            {showPassword ? '' : ''}
        </ShowPasswordIcon>
        </PasswordInputContainer>
        <PasswordInputContainer>
        <PasswordInput
            type={showPassword ? 'text' : 'password'}
            placeholder=" "
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
        />
        <ShowPasswordIcon onClick={() => setShowPassword(!showPassword)}>
            {showPassword ? '' : ''}
        </ShowPasswordIcon>
        </PasswordInputContainer>
        <PasswordInputContainer>
        <PasswordInput
            type={showPassword ? 'text' : 'password'}
            placeholder="  "
            value={confirmNewPassword}
            onChange={(e) => setConfirmNewPassword(e.target.value)}
        />
        <ShowPasswordIcon onClick={() => setShowPassword(!showPassword)}>
            {showPassword ? '' : ''}
        </ShowPasswordIcon>
        </PasswordInputContainer>
        <RequirementText>
        *  : 8 , , ,   .
        </RequirementText>
        <SubmitButton onClick={handleSubmit}></SubmitButton>
    </PasswordChangeContainer>
    );
    };

export default PasswordChange;

