import { Link } from 'react-router-dom';
import styled from 'styled-components';
import MyPageInfo from './mypage-info';
import PasswordChange from './PasswordChange';

const Header = styled.header`
background-color: #488AEE;
// padding: 5px;
height: 130px;
text-align: center;
justify-content: center;
`;


const Logoimg = styled.img`
// padding: 10px;
height: 110px;
text-align: center;
display: inline-block;
cursor: pointer; /*       */
`;
const StyledButton = styled.button`
text-decoration: none;
color: inherit;
background-color: lightGray;
padding: 5px 10px;
margin-right: 15px;
margin-top: 10px;
border: none; /*   */
border-radius: 13px;
display: inline-block;
cursor: pointer;
transition: background-color 0.3s ease-in-out;

font-size: 13px; /*    */
font-weight: 400; /*    */
`;

const Linkdiv = styled.div`
background-color: #488AEE;
text-decoration: none;
color: inherit;
text-align: right;
transition: background-color 0.3s ease-in-out;
`;

const MyLink = styled(Link)`
text-decoration: none;
color: inherit;
padding: 5px 10px;
margin-left: 5px;
display: inline-block;
transition: background-color 0.3s ease-in-out;

font-size: 13px; /*    */
font-weight: 400; /*    */

`;

const Headerlist = styled.div`
background-color: #488AEE;
text-align: right;
padding-bottom: 5px;
padding-right: 10px;
`;

const infoData = {
    userID: 'testid1234',
    userBirth:'2003 1 3',
    userName: 'testnickname',
    userGender: '',
};

const Mypage = () => {
    return (
        <div>
            <Linkdiv>
            <StyledButton></StyledButton>
            </Linkdiv>
        <Header>
        <Link to="/."> {/*   */}
            <Logoimg src="info.png" alt="Logo" />
        </Link>
            <Headerlist>
            
            <MyLink to ="/festival-list"> </MyLink>
            <MyLink to ="/partyall-list">  </MyLink>
            <MyLink to ="/my-party-list"> </MyLink>
            </Headerlist>
            <MyPageInfo infoData={infoData}/>
            <PasswordChange/>



        </Header>
        </div>
        
    );
    };

    export default Mypage;


