// import React from 'react';
import styled from 'styled-components';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import ReviewImage from './reviewImage';


const Mybutton = styled.button`
padding: 3px 8px 3px 8px;
margin: 3px;
margin-bottom: 13px;
font-size: 15px;
border: 1px solid gray;
border-radius: 13px;
background-color: #F1F1F1;
color: #488AEE;
cursor: pointer;
transition: opacity 0.3s ease;
&:hover {
opacity: 0.8;
}
`;
const Divider = styled.div`
border-bottom: 1px solid #ccc;
margin: 20px 0;
`;
const Header = styled.header`
background-color: #488AEE;
// padding: 5px;
height: 130px;
text-align: center;
justify-content: center;
`;

const Logoimg = styled.img`
// padding: 10px;
height: 110px;
text-align: center;
display: inline-block;
cursor: pointer; /*       */
`;

const StyledButton = styled.button`
text-decoration: none;
color: inherit;
background-color: lightGray;
padding: 5px 10px;
margin-right: 15px;
margin-top: 10px;
border: none; /*   */
border-radius: 13px;
display: inline-block;
cursor: pointer;
transition: background-color 0.3s ease-in-out;

font-size: 13px; /*    */
font-weight: 400; /*    */
`;

const Linkdiv = styled.div`
background-color: #488AEE;
text-decoration: none;
color: inherit;
text-align: right;
transition: background-color 0.3s ease-in-out;
`;

const MyLink = styled(Link)`
text-decoration: none;
color: inherit;
padding: 5px 10px;
margin-left: 5px;
display: inline-block;
transition: background-color 0.3s ease-in-out;

font-size: 13px; /*    */
font-weight: 400; /*    */

`;

const Headerlist = styled.div`
background-color: #488AEE;
text-align: right;
padding-bottom: 5px;
padding-right: 10px;
`;


const ImageContainer = styled.div`
    display: flex;
    justify-content: center;
`;

const Reviewdiv = styled.div`
    text-align: center;
    padding: 30px;
`;

const ReviewPage = () => {
    const [partyData, setPartyData] = useState({
        hostNickname: '',
        festivalName: '2023  ',
        partyCurrent: [
            { id: 'id01', district: '', manner: '36.5' },
            { id: 'id02', district: '', manner: '70' },
            { id: 'id03', district: '', manner: '20' },
        ],
    });

    const navigate = useNavigate();

    const handleReviewSubmit = () => {
            alert('  .');
            navigate('/party-list');
        };

    return (
        <div>
            <Linkdiv>
                <StyledButton></StyledButton>
            </Linkdiv>
            <Header>
                <Link to="/."> {/*   */}
                    <Logoimg src="info.png" alt="Logo" />
                </Link>
                <Headerlist>
                    <MyLink to="/festival-list"> </MyLink>
                    <MyLink to="/party-list">  </MyLink>
                    <MyLink to="/my-party-list"> </MyLink>
                </Headerlist>
            </Header>

            {/*    */}
            <Reviewdiv>
                <h3>{partyData.hostNickname}    ?</h3>
                <p> : {partyData.festivalName}</p>
                
                {/* <p>
                    {partyData.partyCurrent.map((participant) => (
                        <span key={participant.id}>
                            {participant.id}    ?{' '}
                        </span>
                    ))}
                </p> */}
                <Divider />
                <p>  !    .</p>
            </Reviewdiv>

            <ImageContainer>
                        <ReviewImage />
                </ImageContainer>
            <div style={{ justifyContent: 'center', display: 'flex' }}>
                <Mybutton onClick={handleReviewSubmit}>  </Mybutton>

            </div>
            
            
        </div>
    );
};

export default ReviewPage;

