//   (partyinfo.js)      
import { useState } from 'react';
import { Form, Button } from 'react-bootstrap';

// import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';


const Header = styled.header`
background-color: #488AEE;
// padding: 5px;
height: 130px;
text-align: center;
justify-content: center;
`;

const Logoimg = styled.img`
// padding: 10px;
height: 110px;
text-align: center;
display: inline-block;
cursor: pointer; /*       */
`;

const StyledButton = styled.button`
text-decoration: none;
color: inherit;
background-color: lightGray;
padding: 5px 10px;
margin-right: 15px;
margin-top: 10px;
border: none; /*   */
border-radius: 13px;
display: inline-block;
cursor: pointer;
transition: background-color 0.3s ease-in-out;

font-size: 13px; /*    */
font-weight: 400; /*    */
`;

const Linkdiv = styled.div`
background-color: #488AEE;
text-decoration: none;
color: inherit;
text-align: right;
transition: background-color 0.3s ease-in-out;
`;

const MyLink = styled(Link)`
text-decoration: none;
color: inherit;
padding: 5px 10px;
margin-left: 5px;
display: inline-block;
transition: background-color 0.3s ease-in-out;

font-size: 13px; /*    */
font-weight: 400; /*    */

`;

const Headerlist = styled.div`
background-color: #488AEE;
text-align: right;
padding-bottom: 5px;
padding-right: 10px;
`;

const StyledForm = styled(Form)`
padding-top: 10px;
margin: 10px;
text-align: center;
`;

const PartyMake = () => {
    const [partyName, setPartyName] = useState('');
    const [recruitment, setRecruitment] = useState('');
    const [partyChatUrl, setChatURL] = useState('');
    const [partyDetail, setDescription] = useState('');
    const [partyTag, setTags] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();

        //        .
        const formData = {
        partyName,
        recruitment,  // 
        partyChatUrl,      // URL
        partyDetail,  //  
        partyTag: partyTag.split(',').map(tag => tag.trim()), //     .
        };
        try {
            const response = await fetch('https://cors-anywhere.herokuapp.com/https://port-0-buife-5mk12alp6foaqx.sel5.cloudtype.app/party/new', {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify(formData),
            });
      
            if (response.ok) {
              const responseData = await response.json();
              console.log('Party created successfully:', responseData);
            } else {
              console.error('Failed to create party:', response.statusText);
            }
          } catch (error) {
            console.error('Error while creating party:', error);
          }
       
};




    return (
        <div>
            <Linkdiv>
            <StyledButton></StyledButton>
            </Linkdiv>
            <Header>
                <Link to="/."> {/*   */}
                    <Logoimg src="info.png" alt="Logo" />
                </Link>
                <Headerlist>
                    <MyLink to ="/festival-list"> </MyLink>
                    <MyLink to ="/party-list">  </MyLink>
                    <MyLink to ="/my-party-list"> </MyLink>
                </Headerlist>
            </Header>

        {/*   */}
        <StyledForm onSubmit={handleSubmit}>
        <Form.Group controlId="partyName" style={ {margin: '20px'}}>
        <Form.Label></Form.Label>
        <Form.Control style={{ textAlign: 'center', width: '50%', justifyContent: 'center', marginLeft: 'auto', marginRight: 'auto', fontSize: '13px'}}
            type="text"
            placeholder=" ."
            value={partyName}
            onChange={(e) => setPartyName(e.target.value)}
            required
        />
        </Form.Group>
        <Form.Group controlId="recruitment" style={ {margin: '20px'}}>
        <Form.Label></Form.Label>
        <Form.Control style={{ textAlign: 'center', width: '50%', justifyContent: 'center', marginLeft: 'auto', marginRight: 'auto', fontSize: '13px'}}
            type="number"
            placeholder="    "
            value={recruitment}
            onChange={(e) => setRecruitment(Math.max(1, parseInt(e.target.value)))}
            required
        />
        </Form.Group>
        <Form.Group controlId="partyChatUrl" style={ {margin: '20px'}}>
        <Form.Label> URL</Form.Label>
        <Form.Control style={{ textAlign: 'center', width: '50%', justifyContent: 'center', marginLeft: 'auto', marginRight: 'auto', fontSize: '13px'}}
            type="url"
            placeholder=" URL  ."
            value={partyChatUrl}
            onChange={(e) => setChatURL(e.target.value)}
            required
        />
        </Form.Group>
        <Form.Group controlId="partyDetail" style={ {margin: '20px'}}>
        <Form.Label>  </Form.Label>
        <Form.Control style={{ textAlign: 'center', width: '50%', justifyContent: 'center', marginLeft: 'auto', marginRight: 'auto', fontSize: '13px'}}
            as="textarea"
            rows={3}
            placeholder="    ."
            value={partyDetail}
            onChange={(e) => setDescription(e.target.value)}
            required
        />
        </Form.Group>
        <Form.Group controlId="partyTag" style={ {margin: '20px'}}>
        <Form.Label>   ( 5)</Form.Label>
        <Form.Control style={{ textAlign: 'center', width: '50%', justifyContent: 'center', marginLeft: 'auto', marginRight: 'auto', fontSize: '13px'}}
            type="text"
            placeholder="  . ( 5)"
            value={partyTag}
            onChange={(e) => setTags(e.target.value)}
            maxLength={50}
        />
        </Form.Group>
        <Button variant="primary" type="submit" style={{ margin: '10px'}  }>
         
        </Button>
    </StyledForm>
        </div>
        
    );
};
export default PartyMake;