import React, { useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
// import './partyPopup.css';
// import { Link } from 'react-router-dom';
// import ReviewPage from './reviewPage';
// import PartyCurrentModal from './PartyCurrentModal';

// const Mylink = styled(Link)`
// padding: 3px 8px 3px 8px;
// margin: 3px;
// margin-bottom: 13px;
// font-size: 15px;
// border: 1px solid gray;
// border-radius: 13px;
// background-color: #F1F1F1;
// color: #488AEE;
// cursor: pointer;
// transition: opacity 0.3s ease;
// &:hover {
// opacity: 0.8;
// }
// `;
const Mybutton = styled.button`
padding: 3px 8px 3px 8px;
margin: 3px;
margin-bottom: 13px;
font-size: 15px;
border: 1px solid gray;
border-radius: 13px;
background-color: #F1F1F1;
color: #488AEE;
cursor: pointer;
transition: opacity 0.3s ease;
&:hover {
opacity: 0.8;
}
`;

const Tagdiv = styled.div`
display: flex;
flex-wrap: wrap;
`;

const Boxdiv = styled.div`
margin-right: 8px;
margin-bottom: 8px;
margin-top: 8px;
padding: 3px;
padding-right: 12px;
padding-left: 12px;
background-color: #F1F1F1;
border: 1px solid gray;
border-radius: 15px;
font-size: 12px;
color: #488AEE;
`;

const Divider = styled.div`
border-bottom: 1px solid #ccc;
margin: 20px 0;
`;
const ReviewContainer = styled.div`
margin-top: 20px;
`;
const PartyPopup = ({ partyData }) => {
const {
hostNickname,
tags,
description,
festivalName,
totalParticipants,
currentParticipants,
deadline,
partyCurrent,
} = partyData;

const [showPartyCurrentInfo, setShowPartyCurrentInfo] = useState(false);
const [showReviewForm, setShowReviewForm] = useState(false);

const navigate = useNavigate();

const handleReview = () => {
    setShowReviewForm(true);
};

const directionReview = () => {
    navigate('/reviewPage');
};

return (
<div>
    <div className="party-header">{hostNickname}   </div>
    <Tagdiv>
    {tags.map((tag, index) => (
        <Boxdiv key={index}>#{tag}</Boxdiv>
    ))}
    </Tagdiv>
    <div>{description}</div>

    <Divider></Divider>

    <div>
    <div> : {festivalName}</div>
    <div> : {totalParticipants} </div>
    <div> : {currentParticipants} </div>
    <div> : {deadline}</div>
    </div>
    <div style={{ justifyContent: 'right', display: 'flex' }}>
    <Mybutton onClick={() => setShowPartyCurrentInfo(!showPartyCurrentInfo)}>
         
    </Mybutton>
    <Mybutton> </Mybutton>
    <Mybutton> </Mybutton>
    <Mybutton onClick={handleReview}></Mybutton>
    </div>
    <Divider></Divider>

    {showPartyCurrentInfo && (
    <div>
        <p>  </p>
        <div>
        <ul>
            {partyCurrent.map((participant) => (
            <li key={participant.id}>
                ID: {participant.id}, District: {participant.district}, Manner: {participant.manner}
                <Mybutton></Mybutton>
                <Mybutton></Mybutton>
            </li>
            ))}
        </ul>
        </div>
    </div>
    )}
    {showReviewForm && (
    <ReviewContainer>
        {/*      */}
        {/*         */}
        <p>   </p>
        <div>
        <ul>
            {partyCurrent.map((participant) => (
            <li key={participant.id}>
                ID: {participant.id}, District: {participant.district}, Manner: {participant.manner}
                <Mybutton style={{marginLeft: '10px'}}onClick={directionReview}></Mybutton>    
            </li>
            ))}
        </ul>
        </div>
    </ReviewContainer>
    )}
</div>
);
};

export default PartyPopup;

